import React, { useState } from 'react';
import Holder from './Holder';
import styled from 'styled-components';
import StudyObject from './StudyObject';

const AboutWrap = styled.div`
  font-family: 'Oswald', sans-serif;
  
  h1 {
    color: green;
  }

  img {
    display: inline-flex;
    width: 100%;
  }
`;

const ImgWrapper = styled.div`
  display: inline-flex;
  width: 28pc;
  margin: 64px;
`;

const Options = styled.div`
  position: absolute;
  width: 50%;
  right: 10px;
  top: 20%;
  padding: 2pc;

  ul {
    list-style: none;
  }

  ul li {
    font-size: 4pc;
  }
`;

const Opt = styled.div`
  padding-bottom: 4pc;
  h4 {
    font-size: 5pc;
    margin: 1pc 0;
    color: gray;
    width: 100%;
    text-align: left;
  }

  p {
    font-size: 20px;
    width: 34pc;
    color: black;
  }
`;

const Content = styled.div`
  padding: 0;
`;

const BtnWrap = styled.div`
  display: flex;
  justify-content: flex-start;
  width: 100%;

  .right-content {
    display: flex;
    width: 100%;
    justify-content: flex-end;
  }

  button {
    width: 7pc;
    font-size: 1pc;
    border: 2px solid #FF5178;
    color: white;
    color: #FF5178;
    padding: 5px;
    border-radius: 20px;
  }
`;

const BackBtn = styled.div`
  button {
    cursor: pointer;
    font-family: 'Oswald', sans-serif;
    background: #FF5178;
    border: none;
    font-size: 2pc;
    width: 14pc;
    padding: 1pc;
    color: white;
  }
`;

const About = () => {
  const [showHolder, setShowHolder] = useState(false);
  const [showStudyObject, setShowStudyObject] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const [showBtnBack, setShowBtnBack] = useState(false);
  
  return (
    <AboutWrap>
      {!showContent ? (
        <Options>
          <Opt>
            <h4>Elección de soporte</h4>
            <p>
              Se eligió como soporte un sitio web con el objetivo de incorporar una 
              nueva herramienta de comunicación accesible desde ...
            </p>
            <BtnWrap>
              <button type="button" onClick={() => {
                  setShowHolder(true);
                  setShowStudyObject(false);
                  setShowContent(true);
                  setShowBtnBack(true);
                }}>Ver mas</button>
            </BtnWrap>
          </Opt>
          <Opt>
            <h4>Objeto de estudio</h4>
            <p>
              El objeto de estudio del trabajo son los mercados  
              latinoamericanos desde los primeros casos donde surgieron ...
            </p>
            <BtnWrap>
              <button onClick={() => {
                setShowHolder(false);
                setShowStudyObject(true);
                setShowContent(true);
                setShowBtnBack(true);
              }}>Ver mas</button>
            </BtnWrap>
          </Opt>
        </Options>
      ) : (
        <Content>
          {showBtnBack ? (
            <BackBtn>
              <button type="button" onClick={() => {
                setShowHolder(false);
                setShowStudyObject(false);
                setShowContent(false);
              }}>Volver</button>
            </BackBtn>
          ) : (
            ''
          )}
          {showHolder ? (
            <Holder />
          ) : (
            ''
          )}
          {showStudyObject ? (
            <StudyObject />
          ) : (
            ''
          )}
        </Content>
      )}

      <ImgWrapper>
        {!showContent ? (
          <img src={`${process.env.PUBLIC_URL}/images/img-2.jpeg`}  alt="img-2" />
        ) : (
          ''
        )}
      </ImgWrapper>
      
    </AboutWrap>
    
  );
}

export default About;